<template>
    <select class="form-select" aria-label="Default select example" name="select" style="width:3.5rem; padding: 0; margin-right: 0.25rem" @change="setNbSubdivisions">
        <option value="1" >1</option>
        <option value="2" >2</option>
        <option value="3">3</option>
        <option value="4" selected>4</option>
        <option value="5">5</option>
        <option value="6">6</option>
        <option value="7">7</option>
        <option value="8">8</option>
        <option value="9">9</option>
        <option value="10">10</option>
        <option value="11">11</option>
        <option value="12">12</option>
    </select>
    <button v-if="$store.state.samplesChosen.length!==1" type="button" class="btn btn-danger" @click="deleteSample">
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
            <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
        </svg>
    </button>
</template>

<script>
export default {
    computed:{
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
        nbBeats(){
            return this.$store.state.nbBeats;
        },
    },
    methods:{
        deleteSample(){
            this.$store.commit('deleteSamplesChosen',this.mySample.id);
        },
        setNbSubdivisions(e){
            var divsActivate = document.getElementsByName("noteOf"+this.mySample.name+this.mySample.id);
            for(var i=0; i<divsActivate.length; i++){
                divsActivate[i].classList.remove("active1");
                divsActivate[i].classList.remove("active2");
                divsActivate[i].classList.remove("active3");
            }
            this.$store.commit("changeTrack", {sampleId: this.mySample.id, newSubDiv: parseInt(e.target.value)});                          
        }
    },
    props:['name','id','src'],
}
</script>

