<template>
    <h1 class="mx-3 mb-3" style="color: ghostwhite">BaSon</h1>
    <div class="mx-3 mb-5 master-control">
        <span class="master-control-label">MASTER CONTROL</span>
        <div style="display: grid; width: 100%; grid-template-columns: 8rem 9rem 8rem 17rem 17rem 1rem; align-items: start;">
            <div class="float-start me-2">
                <button class="btn btn-success buttonAction" id="btnPlay" type="button" @click="changeButtonAction">
                    <div class="float-start" style="margin-top:2px">
                        <p style="margin : 3px 8px 0 auto">PLAY</p>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-play-fill" viewBox="0 0 16 16" style="margin-top:2px">
                        <path d="m11.596 8.697-6.363 3.692c-.54.313-1.233-.066-1.233-.697V4.308c0-.63.692-1.01 1.233-.696l6.363 3.692a.802.802 0 0 1 0 1.393z"/>
                    </svg>
                </button>
                    <button class="btn btn-danger buttonAction d-none" id="btnPause" type="button" @click="changeButtonAction">
                    <div class="float-start" style="margin-top:2px">
                        <p style="margin : 3px 8px 0 auto">STOP</p>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-stop-fill" viewBox="0 0 16 16" style="margin-top:2px">
                        <path d="M5 3.5h6A1.5 1.5 0 0 1 12.5 5v6a1.5 1.5 0 0 1-1.5 1.5H5A1.5 1.5 0 0 1 3.5 11V5A1.5 1.5 0 0 1 5 3.5z"/>
                    </svg>
                </button>
            </div>
            <div>
                <button type="button" class="btn btn-danger buttonActionRecord" @click="startRecord" id="recorderButton">
                    <div class="float-start"  style="margin-top:2px" > 
                        <p style="margin: 3px 8px 0 auto" id="record">RECORD</p>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-record-fill" viewBox="0 0 16 16" style="margin-top:2px">
                        <path fill-rule="evenodd" d="M8 13A5 5 0 1 0 8 3a5 5 0 0 0 0 10z"/>
                    </svg>
                </button>
            </div>
           <div class="divInputBpm">
                <label class="labelInputBpm" color="gray" font-size="0.6rem" font-weight="600" letter-spacing="0.1em" for="bpm">BPM</label>
                <input class="bpm-text-input inputBpm" min="30" max="300" font-size="1.5rem" font-weight="500" id="bpm" color="brightRed" type="number" v-model.number="bpm"  @blur="updateInput">
                <div class="divInputButtons" display="flex">
                    <button class="inputButton" id="inputButtonUp" color="white" width="2rem" aria-label="Increase beat per minute" font-weight="bold" @click="upBpm">
                        <svg width="10px" height="6px" viewBox="0 0 12 7" version="1.1" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink">
                            <polygon points="0 7 12 7 6 0" fill="white"></polygon>
                        </svg>
                    </button>
                    <button class="inputButton" id="inputButtonDown" color="white" width="2rem" aria-label="Decrease beat per minute" font-weight="bold" @click="downBpm">
                        <svg width="10px" height="6px" viewBox="0 0 12 7" version="1.1" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink">
                        <polygon points="0 0 12 0 6 7" fill="white"></polygon>
                        </svg>
                    </button>
                </div>
           </div>
           <div>
            <MasterScope />
           </div>
        </div>
    </div>
</template>

<script>
import MasterScope from '@/components/MasterScope.vue'

export default ({
    name: "Master",
    components: {
        MasterScope
    },
    computed: { 
        bpm: {
            get(){
                return this.$store.state.bpm;
            },
            set(newBpm){
                if(newBpm > 300){
                    newBpm = 300;
                    this.$store.commit("setBpm", newBpm);
                    this.$forceUpdate();
                    return;
                }
                else if(newBpm < 30){
                    newBpm = 30;
                }
                this.$store.commit("setBpm", newBpm);
                
            }
        },
        nbBeats(){
            return this.$store.state.nbBeats;
        },
    },
    methods: {
        startRecord(){
            this.$store.dispatch("startRecord");
        },
        updateInput(e){
            if(e.target.value < 30){
                e.target.value = 30;
            }
        },
        setBeats(e){
            this.$store.commit('updateNbBeats', parseInt(e.target.value));
        },
        changeButtonAction(){
            this.$store.dispatch('play');
            if(document.getElementById("btnPlay").classList.contains("d-none")){    
                var btnPause = document.getElementById("btnPause");
                btnPause.classList.add("d-none");
                btnPause.disabled = true;
                var btnPlay = document.getElementById("btnPlay");
                btnPlay.classList.remove("d-none");
                btnPlay.disabled = false;
                document.getElementById("progress").style.width = "0%";
            }
            else{
                var btnPlay = document.getElementById("btnPlay")
                btnPlay.classList.add("d-none");
                btnPlay.disabled = true;
                var btnPause = document.getElementById("btnPause");
                btnPause.classList.remove("d-none");
                btnPause.disabled = false;
            }   
        },
        upBpm(){
            var value = parseInt(document.getElementById('bpm').value, 10);
            value = isNaN(value) ? 0 : value;
            value++;
            if(value > 300){
                return;
            }
            document.getElementById('bpm').value = value;
            document.getElementById("inputButtonUp").blur();
            this.$store.commit('setBpm',value);
        },
        downBpm(){
            var value = parseInt(document.getElementById('bpm').value, 10);
            value = isNaN(value) ? 0 : value;
            value--;
            if(value < 30){
                return;
            }
            document.getElementById('bpm').value = value;
            document.getElementById("inputButtonDown").blur();
            this.$store.commit('setBpm',value);
        },
        
    },
    mounted() {
        window.addEventListener('keyup', event=>{
            if (event.code === 'Space') {
                event.preventDefault();
                this.changeButtonAction();
            }
        });
        window.addEventListener('keydown', event =>{
            if (event.code === 'Space') {
                event.preventDefault();
            }
        })
  },
})

</script>

