<template>
    <div style="display: flex;">
    <div style="height: 400px; margin-top: 1rem;">
            <select class="form-select" aria-label="Default select example" name="selectFilter" style="width: 8rem; height: 2rem; margin-left: 0.5rem" @change="setFilter">
                <option value="allpass" selected>Allpass</option>
                <option value="lowpass" >Lowpass</option>
                <option value="highpass">Highpass</option>
                <option value="bandpass">Bandpass</option>
            </select>
            <div class="control-slot" style="margin: 1rem 0 0 0.5rem;">
                    <div class="knob-slot frequencySlider">
                        <span class="knob-name" style="margin-left: -5px; margin-bottom: 5px;">Freq</span>
                        <div style="display: flex">
                            <div class="knob">
                                <webaudio-slider
                                    :src="freqKnobData.imageURL"
                                    :knobsrc="freqKnobData.imageButtonUrl"
                                    :min="freqKnobData.min"
                                    :max="freqKnobData.max"
                                    :width="freqKnobData.width"
                                    :height="freqKnobData.height"
                                    :step="freqKnobData.step"
                                    :id="'freq'+id"
                                    :valuetip="1"
                                    conv="(Math.pow(10,x)*20).toFixed(0)"
                                    direction="vert"
                                    @input="setFrequencyFilter">
                                </webaudio-slider>
                            </div>
                            <div style="height: 200px; font-size: 13px;"> 
                                <p style="margin: -5px 0 0 3px">-20kHz</p>
                                <p style="margin: 4px 0 0 3px">-10kHz</p>
                                <p style="margin: 1px 0 0 3px">-5kHz</p>
                                <p style="margin: 9px 0 0 3px">-2kHz</p>
                                <p style="margin: 1px 0 0 3px">-1kHz</p>
                                <p style="margin: 2px 0 0 3px">-500Hz</p>
                                <p style="margin: 9px 0 0 3px">-200Hz</p>
                                <p style="margin: 1px 0 0 3px">-100Hz</p>
                                <p style="margin: 1px 0 0 3px">-50Hz</p>
                                <p style="margin: 7px 0 0 3px">-20Hz</p>
                            </div>
                        </div>
                    </div>
                    <div class="separation">
                    </div>
                    <div class="knob-slot">
                        <span class="knob-name">Q</span>
                        <div class="knob">
                            <span class="knob-label">0.01</span>
                            <webaudio-knob
                                :src="QKnobData.imageURL"
                                :knobsrc="QKnobData.imageButtonUrl"
                                :min="QKnobData.min"
                                :max="QKnobData.max"
                                :width="QKnobData.size"
                                :height="QKnobData.size"
                                :step="QKnobData.step"
                                :id="'Q'+id"
                                :value="1"
                                :valuetip="1"
                                @input="setQFilter">
                            </webaudio-knob>
                            <span class="knob-label">10.00</span>
                        </div> 
                    </div>
            </div>
        </div>
        <div style="display: flex; margin: 1rem 0 0 14.5rem;">
            <div style="height: 300px; font-size; color: ghostwhite">   
                <p style="margin: -5px 3px 0 2px">20kHz -</p>
                <p style="margin: 6px 3px 0 2px">10kHz -</p>
                <p style="margin: 5px 3px 0 10px">5kHz -</p>
                <p style="margin: 12px 3px 0 10px">2kHz -</p>
                <p style="margin: 6px 3px 0 10px">1kHz -</p>
                <p style="margin: 3px 3px 0 0">500Hz -</p>
                <p style="margin: 14px 3px 0 0">200Hz -</p>
                <p style="margin: 5px 3px 0 0">100Hz -</p>
                <p style="margin: 3px 3px 0 8px">50Hz -</p>
                <p style="margin: 13px 3px 0 8px">20Hz -</p>
            </div>
            <div style="background-color: black; height: 300px; display:flex;" :style="{width: 14*$store.state.nbBeats + (mySample.timeSubdiv*$store.state.nbBeats*0.5) + ($store.state.nbBeats-1)*0.5 + 'vw'}" id="containerAutomation">
                <PointAutomation v-for="n in ($store.state.nbBeats*14)" :key="n"  :id="mySample.id" :idPoint="n" :x="n" :y="(3-Math.log10(frequency/20))*93.33" :freqInitial="frequency"/>
            </div>
        </div>
    </div>
</template>

<script>
import '@/assets/js/webaudio-controls.js'
import freqKnobImage from '@/assets/images/knobs/roland_SH_101_knob.png'
import buttonSLiderImage from '@/assets/images/knobs/Kop.png'
import QKnobImage from '@/assets/images/knobs/maschine-default.png'
import PointAutomation from '@/components/PointAutomation.vue'
export default ({
    components: {
        PointAutomation
    },
    data(){
        return{
            frequency: 20,
            Q: 1,
            freqKnobData: {imageURL:freqKnobImage, imageButtonUrl: buttonSLiderImage, min:0, max:3, step:0.0001, width:20, height: 220},
            QKnobData: {imageURL:QKnobImage, min:0.01, max:10, step:0.01, size: 40},
            canvasData: {height:300, range:500},
            cercle: [],
            recCanvas:null,
        };
    },
    props:['id'],
    computed:{
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
        frequencyChange(){
            return this.mySample.fxParams.frequency;
        }
    },
    methods:{
        setFilter(e){
            var sample = this.mySample;
            sample.audioNode.filter.type = e.target.value;
            var freq = parseFloat(document.getElementById("freq" + sample.id).value);
            freq = (Math.pow(10,freq)*20).toFixed(0);
            sample.audioNode.filter.frequency.value = freq;
            sample.audioNode.filter.Q.value = parseFloat(document.getElementById("Q" + sample.id).value);
        },
        setFrequencyFilter(){
            if(this.mySample.sliderFreqDisabled){
                var sample = this.mySample;
                sample.audioNode.filter.frequency.value = sample.fxParams.frequency;
                console.log(sample.fxParams.frequency);
                var idSliderFreq = 'freq' + this.id
                var sliderFreq = document.getElementById(idSliderFreq);
                sliderFreq.value = Math.log10(sample.fxParams.frequency/20);
            }
            else{
                var sample = this.mySample;
                var freq = parseFloat(document.getElementById("freq" + sample.id).value);
                freq = (Math.pow(10,freq)*20).toFixed(0);
                sample.audioNode.filter.frequency.value = freq;
                this.frequency = freq;
            }
            
        },
        setQFilter(){
            var sample = this.mySample;
            sample.audioNode.filter.Q.value = parseFloat(document.getElementById("Q" + sample.id).value);
            this.Q = parseFloat(document.getElementById("Q" + sample.id).value);
        },
        
    },
    watch: {
        frequencyChange(newVal, oldVal){
            var sample = this.mySample;
            sample.audioNode.filter.frequency.value = newVal;
            var idSliderFreq = 'freq' + this.id
            var sliderFreq = document.getElementById(idSliderFreq);
            sliderFreq.value = Math.log10(newVal/20);
            
        }
    },
})
</script>

